html {
  background-color: rgb(226, 226, 226);
}

.app-wrapper {
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
    'h h'
    'n c';
}

.app-wrapper-content {

  grid-area: c;
  background-color: #fff;
  margin-top: 10px;
  width: 80%;
}